@font-face{font-family:OpenSans;src:url(assets/fonts/OpenSans-Regular.ttf)}@font-face{font-family:SF-Big;src:url(assets/fonts/SFUIDisplay-Black.ttf)}@font-face{font-family:SF-Bold;src:url(assets/fonts/SFUIDisplay-Bold.ttf)}@font-face{font-family:SF;src:url(assets/fonts/SFUIDisplay-Regular.ttf)}*{margin:0;padding:0;-webkit-box-sizing:border-box;box-sizing:border-box;-webkit-tap-highlight-color:transparent}body{font-family:OpenSans,sans-serif;background:#fcfcfc;color:#222}ul{list-style-type:none}a{text-decoration:none}h2{font-family:SF-Bold,sans-serif;font-size:36px}h2,h3{text-align:center}h3{font-size:18px;color:#324148;font-weight:200;margin:40px 30% 70px}.container{margin:170px 160px 0;position:relative}.bg{position:absolute}.offer__body{display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:55px 30px}.offer__body-item{border-radius:10px;border:1px solid transparent;max-width:352px;background:#fff;margin:0 auto}.offer__body-item,.offer__body-item h4{-webkit-transition:.4s;-o-transition:.4s;transition:.4s}.offer__body-item h4{margin:20px 0;font-size:18px;font-family:SF-Bold,sans-serif;padding:0 20px}.offer__body-item p{font-size:16px;color:#393939;padding:0 20px 20px}.offer__body-item:hover{border:1px solid #339f61;-webkit-box-shadow:0 21px 23px -4px rgba(51,159,97,.34);box-shadow:0 21px 23px -4px rgba(51,159,97,.34)}.offer__body-item:hover h4{color:#339f61}.bg-offer-1{top:-125px;left:-160px}.bg-offer-2{top:50%;right:-160px}.bg-offer-3{top:50%;left:-160px}.bg-offer-4{bottom:-220px;right:-160px}